.Switch_switch__2HoI0 {
    position: relative;
    display: inline-block;
    width: 40px;
    height: 20px;
    margin: 0;
}

.Switch_switch__2HoI0 input { 
    opacity: 0;
    width: 0;
    height: 0;
}

.Switch_switch__2HoI0 span {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    transition: .4s;
}

.Switch_switch__2HoI0 span:before {
    position: absolute;
    content: "";
    height: 16px;
    width: 16px;
    left: 4px;
    bottom: 2px;
    background-color: white;
    transition: .4s;
}

.Switch_switch__2HoI0 input:checked + span {
    background-color: #2196F3;
}

.Switch_switch__2HoI0 input:focus + span {
    box-shadow: 0 0 1px #2196F3;
}

.Switch_switch__2HoI0 input:checked + span:before {
    -webkit-transform: translateX(17px);
    transform: translateX(17px);
}

/* Rounded sliders */
.Switch_switch__2HoI0 span {
    border-radius: 34px;
}

.Switch_switch__2HoI0 span:before {
    border-radius: 50%;
}
